.TodoSearch-container{
  display: flex;
  justify-content: center;
  align-items: center;
  
}

.TodoSearch {
  background: #F1F1F1;
  border-radius: 2px;
  border: 2px solid #202329;
  margin: 0;
  height: 64px;
  width: calc(100% - 62px);
  font-size: 24px;
  text-align: center;
  font-family: 'Montserrat';
  font-weight: 400;
  color: #1E1E1F;
  box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5);
  max-width: 500px;
}

.TodoSearch::placeholder {
  color: #A5A5A5;
  font-family: 'Montserrat';
  font-weight: 400;
}

.TodoSearch:focus {
  outline-color: #61DAFA;
}

.TodoSearch:disabled {
  opacity: 25%;
}