.TodoItem {
  background-color: #F1F1F1;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 24px;
  margin-left: auto;
  margin-right: auto;
  box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.15);
  border-radius: 5px;
  color : #393E41;
  max-width: 500px;
}

.TodoItem-p {
  margin: 24px 0 24px 24px;
  width: calc(100% - 120px);
  font-size: 18px;
  line-height: 24px;
  font-weight: 400;
  overflow: hidden;
}
.TodoItem-p--complete {
  text-decoration: line-through;
}

